*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.loading-screen {
  z-index: 10;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: tranparent;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
  }

  & .loader {
    z-index: 20;
    width: 40px;
    aspect-ratio: 1;
    background:
      linear-gradient(#00AAFF 0 0),
      linear-gradient(#FF00AA 0 0),
      #00AAFF;
    background-repeat: no-repeat;
    animation: l1 1.5s infinite; 
  }
}

.loading-panel {
  z-index: 10;
  position: relative;
  left: 0;
  top: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  background: tranparent;

  & .loader {
    z-index: 20;
    width: 5px;
    aspect-ratio: 1;
    background:
      linear-gradient(#0E94A0 0 0),
      linear-gradient(#FFF 0 0),
      #0E94A0;
    background-repeat: no-repeat;
    animation: l1 2s infinite;

    &:nth-child(4n-3) {
      animation-delay: 0.25s;
    }

    &:nth-child(4n-2) {
      animation-delay: 0.5s;
    }
  
    &:nth-child(4n-1) {
      animation-delay: 0.75s;
    }
  
    &:nth-child(4n) {
      animation-delay: 1s;
    }
  }
}

@keyframes l1 {
  0%   {background-size:100%    0,0    100%}
  30%,
  50%  {background-size:100%    0,100% 100%}
  80%,
  100% {background-size:100% 100%,100% 100%}
}